"use client";

import { useEffect, useState } from "react";
import { createClient } from "@/lib/supabase/client";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";

export default function TopicPage({ topicId, studentId }: { topicId: string; studentId: string }) {
  const supabase = createClient();

  const [topic, setTopic] = useState<any>(null);
  const [materials, setMaterials] = useState<any[]>([]);
  const [progress, setProgress] = useState<any>(null);
  const [revisions, setRevisions] = useState<any[]>([]);
  const [testScore, setTestScore] = useState<number>(0);
  const [loading, setLoading] = useState(true);

  async function loadData() {
    setLoading(true);

    const [{ data: topicData }, { data: materialData }, { data: progressData }, { data: revData }] =
      await Promise.all([
        supabase.from("topics").select("*").eq("id", topicId).single(),
        supabase.from("study_materials").select("*").eq("topic_id", topicId),
        supabase
          .from("topic_progress")
          .select("*")
          .eq("topic_id", topicId)
          .eq("student_id", studentId)
          .single(),
        supabase
          .from("revision_schedule")
          .select("*")
          .eq("topic_id", topicId)
          .eq("student_id", studentId)
          .order("revision_number"),
      ]);

    setTopic(topicData);
    setMaterials(materialData || []);
    setProgress(progressData);
    setRevisions(revData || []);

    setLoading(false);
  }

  async function completeVideo() {
    await supabase.rpc("complete_topic_video", {
      p_student: studentId,
      p_topic: topicId,
    });
    loadData();
  }

  async function submitTestScore() {
    await supabase.rpc("complete_topic_test", {
      p_student: studentId,
      p_topic: topicId,
      p_score: testScore,
    });
    loadData();
  }

  async function completeRevision(rev: number) {
    await supabase.rpc("complete_topic_revision", {
      p_student: studentId,
      p_topic: topicId,
      p_rev: rev,
    });
    loadData();
  }

  useEffect(() => {
    loadData();
  }, [topicId]);

  if (loading) return <div className="p-8">Loading...</div>;

  const video = materials.find((m) => m.content_type === "video");
  const pdf = materials.find((m) => m.content_type === "pdf");

  return (
    <div className="max-w-4xl mx-auto p-6 space-y-6">
      {/* HEADER */}
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl">{topic.name}</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="mb-4 text-muted-foreground">{topic.description}</p>

          <div className="flex items-center justify-between mb-2">
            <span>Status:</span>
            <Badge>{progress?.status || "NOT STARTED"}</Badge>
          </div>

          <Progress value={progress?.progress_percentage || 0} />

          <div className="text-right mt-2 text-sm">
            {progress?.total_xp || 0} XP / 200 XP
          </div>
        </CardContent>
      </Card>

      {/* VIDEO SECTION */}
      <Card>
        <CardHeader>
          <CardTitle>üì∫ Lecture Video</CardTitle>
        </CardHeader>
        <CardContent className="space-y-3">
          {video ? (
            <>
              <Button asChild>
                <a href={video.resource_url} target="_blank">
                  Watch Video
                </a>
              </Button>

              {!progress?.video_completed && (
                <Button onClick={completeVideo} className="w-full">
                  ‚úì Mark Video Complete (+40 XP)
                </Button>
              )}
            </>
          ) : (
            <p className="text-muted-foreground">No video available</p>
          )}
        </CardContent>
      </Card>

      {/* NOTES SECTION */}
      <Card>
        <CardHeader>
          <CardTitle>üìÑ Notes / PDF</CardTitle>
        </CardHeader>
        <CardContent className="space-y-3">
          {pdf ? (
            <Button asChild>
              <a href={pdf.resource_url} target="_blank">
                Open Notes PDF
              </a>
            </Button>
          ) : (
            <p className="text-muted-foreground">No notes available</p>
          )}
        </CardContent>
      </Card>

      {/* TEST SECTION */}
      <Card>
        <CardHeader>
          <CardTitle>üìù Test</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <p>Previous Score: {progress?.test_score ?? "Not attempted"}</p>

          <input
            type="number"
            min={0}
            max={20}
            value={testScore}
            onChange={(e) => setTestScore(parseInt(e.target.value))}
            className="border p-2 rounded w-full"
            placeholder="Enter score out of 20"
          />

          <Button onClick={submitTestScore} className="w-full">
            Submit Test Score (+score √ó 5 XP)
          </Button>
        </CardContent>
      </Card>

      {/* REVISION SECTION */}
      <Card>
        <CardHeader>
          <CardTitle>üîÅ Revisions</CardTitle>
        </CardHeader>
        <CardContent className="space-y-3">
          {revisions.map((rev) => (
            <div
              key={rev.revision_number}
              className="flex justify-between items-center border p-3 rounded"
            >
              <div>
                <div className="font-medium">Revision {rev.revision_number}</div>
                <div className="text-xs text-muted-foreground">
                  Due: {rev.due_date}
                </div>
              </div>

              {rev.is_completed ? (
                <Badge>Completed</Badge>
              ) : (
                <Button onClick={() => completeRevision(rev.revision_number)}>
                  Train Now (+15 XP)
                </Button>
              )}
            </div>
          ))}
        </CardContent>
      </Card>
    </div>
  );
}
